import React from 'react'
import TableRow from '@material-ui/core/TableRow'
import TableCell from '@material-ui/core/TableCell'
import InputBase from '@material-ui/core/InputBase'
import { StatusSelectComponent, TextInputComponent, NumberInputComponent } from '../InputComponents/InputComponents'
import classes from './RowComponent.module.scss'

const RowComponent = props => {
  const { pos = 0, rowObj = {}, isEdit = false, onChange } = props
  return (
    <React.Fragment>
      <TableRow className={classes.tableBodyRow}>
        <TableCell align='left'>
          {isEdit ? (
            <StatusSelectComponent
              id={`${rowObj.id}Status`}
              value={rowObj.status}
              error={rowObj.error.status ? true : false}
              onChange={e => {
                onChange('status', e.target.value)
              }}
            />
          ) : (
            rowObj.status
          )}
        </TableCell>
        <TableCell align='left'>{rowObj.id}</TableCell>
        <TableCell align='left'>{rowObj.serviceName}</TableCell>
        <TableCell align='left'>
          {isEdit ? (
            <TextInputComponent
              id={`${rowObj.id}Address`}
              value={rowObj.address}
              error={rowObj.error.address ? true : false}
              onChange={e => {
                onChange('address', e.target.value)
              }}
            />
          ) : (
            rowObj.address
          )}
        </TableCell>
        <TableCell align='left'>
          {isEdit ? (
            <TextInputComponent
              id={`${rowObj.id}ContactPerson`}
              value={rowObj.contactPerson}
              error={rowObj.error.contactPerson ? true : false}
              onChange={e => {
                onChange('contactPerson', e.target.value)
              }}
            />
          ) : (
            rowObj.contactPerson
          )}
        </TableCell>
        <TableCell align='left'>
          {isEdit ? (
            <NumberInputComponent
              id={`${rowObj.id}ContactNumber`}
              value={rowObj.contactNumber}
              error={rowObj.error.contactNumber ? true : false}
              onChange={value => {
                onChange('contactNumber', value)
              }}
            />
          ) : (
            rowObj.contactNumber
          )}
        </TableCell>
        <TableCell align='left'>{rowObj.tin}</TableCell>
      </TableRow>
    </React.Fragment>
  )
}

export default React.memo(RowComponent, (prevProps, nextProps) => {
  try {
    if (
      prevProps.isEdit === nextProps.isEdit &&
      prevProps.rowObj.status === nextProps.rowObj.status &&
      prevProps.rowObj.address === nextProps.rowObj.address &&
      prevProps.rowObj.contactPerson === nextProps.rowObj.contactPerson &&
      prevProps.rowObj.contactNumber === nextProps.rowObj.contactNumber &&
      prevProps.rowObj.error.address === nextProps.rowObj.error.address &&
      prevProps.rowObj.error.contactPerson === nextProps.rowObj.error.contactPerson &&
      prevProps.rowObj.error.contactNumber === nextProps.rowObj.error.contactNumber
    ) {
      return true
    }
    return false
  } catch (err) {
    return false
  }
})
