import React from 'react'
import Modal from '@material-ui/core/Modal'
import Table from '@material-ui/core/Table'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableContainer from '@material-ui/core/TableContainer'
import TableRow from '@material-ui/core/TableRow'
import TableBody from '@material-ui/core/TableBody'
import Button from '@material-ui/core/Button'
import CircularProgress from '@material-ui/core/CircularProgress'
import ProfileService from 'Services/Api/Partner/ProfileService'
import PartnerHeaderCell from '../../../Components/PartnerHeaderCell/PartnerHeaderCell'
import TablePagination from '../../../Components/TablePagination/TablePagination'
import { arrayObjectNullFilter, arrayObjectDistinct } from 'Utilities/Partner/ArrayUtilities'
import useDebounce from '../../../Components/CustomHooks/useDebounce'
import { useHistory } from 'react-router-dom'

import classes from './FacilityListView.module.scss'

const DoctorListView = props => {
  const { open, setOpen, paginationText = '', url = '', type = '' } = props

  const history = useHistory()
  const [isLoaded, setIsLoaded] = React.useState(false)
  const [filter, setFilter] = React.useState({})

  const [status, setStatus] = React.useState({
    field: 'status'
  })
  const [id, setID] = React.useState({
    field: 'id',
    loading: false,
    value: '',
    inputValue: '',
    options: []
  })
  const [doctorName, setDoctorName] = React.useState({
    field: 'full_name',
    loading: false,
    value: '',
    inputValue: '',
    options: []
  })
  const [specialization, setSpecialization] = React.useState({
    field: 'specialization',
    loading: false,
    value: '',
    inputValue: '',
    options: []
  })

  const [facilityName, setFacilityName] = React.useState({
    field: 'facilityName',
    loading: false,
    value: '',
    inputValue: '',
    options: []
  })

  const [clinicSchedule, setClinicSchedule] = React.useState({
    field: 'clinicSchedule',
    loading: false,
    value: '',
    inputValue: '',
    options: []
  })

  const [tableData, setTableData] = React.useState({
    loading: false,
    data: [],
    page: 1,
    pageCount: 1,
    limit: 10
  })

  const IDInput = useDebounce(id.inputValue, 500)
  const facilityNameInput = useDebounce(facilityName.inputValue, 500)
  const doctorNameInput = useDebounce(doctorName.inputValue, 500)
  const specializationInput = useDebounce(specialization.inputValue, 500)
  const clinicScheduleInput = useDebounce(clinicSchedule.inputValue, 500)

  React.useEffect(() => {
    setIsLoaded(true)
  }, [])

  React.useEffect(() => {
    let attached = true
    const fetchDataID = async () => {
      setID({ ...id, loading: true })
      const { data } = await ProfileService.getDoctorsField({
        type: type,
        field: id.field,
        value: id.inputValue,
        filters: filter
      })
      if (attached) {
        let newData = arrayObjectNullFilter(data.data, id.field)
        newData = arrayObjectDistinct(newData, id.field)
        setID({ ...id, loading: false, options: newData })
      }
    }
    if (id.inputValue.length >= 6) {
      fetchDataID()
    }
    return () => {
      attached = false
    }
  }, [IDInput])

  React.useEffect(() => {
    let attached = true
    const fetchDataFacilityName = async () => {
      setFacilityName({ ...facilityName, loading: true })
      const { data } = await ProfileService.getDoctorsField({
        type: type,
        field: 'facility_info.name',
        value: facilityName.inputValue,
        filters: filter
      })
      if (attached && data.data) {
        let newData = data.data.map(obj => {
          return {
            facilityName: obj.facility_info[0].name
          }
        })
        newData = arrayObjectNullFilter(newData, facilityName.field)
        newData = arrayObjectDistinct(newData, facilityName.field)

        setFacilityName({ ...facilityName, loading: false, options: newData })
      }
    }
    if (facilityName.inputValue.length >= 3) {
      fetchDataFacilityName()
    }
    return () => {
      attached = false
    }
  }, [facilityNameInput])

  React.useEffect(() => {
    let attached = true
    const fetchDataDoctorName = async () => {
      setDoctorName({ ...doctorName, loading: true })
      const { data } = await ProfileService.getDoctorsField({
        type: type,
        field: doctorName.field,
        value: doctorName.inputValue,
        filters: filter
      })
      if (attached) {
        let newData = arrayObjectNullFilter(data.data, doctorName.field)
        newData = arrayObjectDistinct(newData, doctorName.field)
        setDoctorName({ ...doctorName, loading: false, options: newData })
      }
    }
    if (doctorName.inputValue.length >= 3) {
      fetchDataDoctorName()
    }
    return () => {
      attached = false
    }
  }, [doctorNameInput])

  React.useEffect(() => {
    let attached = true
    const fetchDataSpecialization = async () => {
      setSpecialization({ ...specialization, loading: true })
      const { data } = await ProfileService.getDoctorsField({
        type: type,
        field: 'specialization.main',
        value: specialization.inputValue,
        filters: filter
      })
      if (attached && data.data) {
        let newData = data.data.map(obj => {
          return {
            specialization: obj.specialization.main
          }
        })
        newData = arrayObjectNullFilter(newData, specialization.field)
        newData = arrayObjectDistinct(newData, specialization.field)
        setSpecialization({ ...specialization, loading: false, options: newData })
      }
    }
    if (specialization.inputValue.length >= 3) {
      fetchDataSpecialization()
    }
    return () => {
      attached = false
    }
  }, [specializationInput])

  React.useEffect(() => {
    let attached = true
    const fetchDataClinicSchedule = async () => {
      setClinicSchedule({ ...clinicSchedule, loading: true })
      const { data } = await ProfileService.getDoctorsField({
        type: type,
        field: 'facility_info.clinic_schedule',
        value: clinicSchedule.inputValue,
        filters: filter
      })
      if (attached && data.data) {
        let newData = data.data.map(obj => {
          return {
            clinicSchedule: obj.facility_info[0].clinic_schedule
          }
        })
        newData = arrayObjectNullFilter(newData, clinicSchedule.field)
        newData = arrayObjectDistinct(newData, clinicSchedule.field)
        setClinicSchedule({ ...clinicSchedule, loading: false, options: newData })
      }
    }
    if (clinicSchedule.inputValue.length >= 3) {
      fetchDataClinicSchedule()
    }
    return () => {
      attached = false
    }
  }, [clinicScheduleInput])

  React.useEffect(() => {
    let attached = true
    const fetchData = async () => {
      setTableData({ ...tableData, loading: true })
      const { data } = await ProfileService.getAllDoctors({
        type,
        limit: tableData.limit,
        page: tableData.page,
        filters: filter
      })
      if (attached) {
        let listOfDoctors = data.data ? data.data.list : []
        const count = data.data ? data.data.count : 0
        if (listOfDoctors) {
          listOfDoctors = listOfDoctors.map(obj => {
            return {
              status: obj.status,
              id: obj.id,
              doctorName: obj.full_name,
              specialization: obj.specialization,
              name: obj.facility ? obj.facility.name : '',
              clinicSchedule: obj.facility ? obj.facility.clinic_schedule : ''
            }
          })
        }
        setTableData({ ...tableData, loading: false, data: listOfDoctors, pageCount: count, page: 1 })
      }
    }

    const fetchDataID = async () => {
      setID({ ...id, loading: true })
      const { data } = await ProfileService.getDoctorsField({
        type: type,
        field: id.field,
        value: id.value,
        filters: filter
      })
      if (attached) {
        let newData = arrayObjectNullFilter(data.data, id.field)
        newData = arrayObjectDistinct(newData, id.field)
        setID({ ...id, loading: false, options: newData })
      }
    }

    const fetchDataFacilityName = async () => {
      setFacilityName({ ...facilityName, loading: true })
      const { data } = await ProfileService.getDoctorsField({
        type: type,
        field: 'facility_info.name',
        value: facilityName.value,
        filters: filter
      })
      if (attached && data.data) {
        let newData = data.data.map(obj => {
          return {
            facilityName: obj.facility_info[0].name
          }
        })
        newData = arrayObjectNullFilter(newData, facilityName.field)
        newData = arrayObjectDistinct(newData, facilityName.field)
        setFacilityName({ ...facilityName, loading: false, options: newData })
      }
    }

    const fetchDataDoctorName = async () => {
      setDoctorName({ ...doctorName, loading: true })
      const { data } = await ProfileService.getDoctorsField({
        type: type,
        field: doctorName.field,
        value: doctorName.value,
        filters: filter
      })
      if (attached) {
        let newData = arrayObjectNullFilter(data.data, doctorName.field)
        newData = arrayObjectDistinct(newData, doctorName.field)
        setDoctorName({ ...doctorName, loading: false, options: newData })
      }
    }

    const fetchDataSpecialization = async () => {
      setSpecialization({ ...specialization, loading: true })
      const { data } = await ProfileService.getDoctorsField({
        type: type,
        field: 'specialization.main',
        value: specialization.value,
        filters: filter
      })
      if (attached && data.data) {
        let newData = data.data.map(obj => {
          return {
            specialization: obj.specialization.main
          }
        })
        newData = arrayObjectNullFilter(newData, specialization.field)
        newData = arrayObjectDistinct(newData, specialization.field)
        setSpecialization({ ...specialization, loading: false, options: newData })
      }
    }

    const fetchDataClinicSchedule = async () => {
      setClinicSchedule({ ...clinicSchedule, loading: true })
      const { data } = await ProfileService.getDoctorsField({
        type: type,
        field: 'facility_info.clinic_schedule',
        value: clinicSchedule.value,
        filters: filter
      })
      if (attached && data.data) {
        let newData = data.data.map(obj => {
          return {
            clinicSchedule: obj.facility_info[0].clinic_schedule
          }
        })
        newData = arrayObjectNullFilter(newData, clinicSchedule.field)
        newData = arrayObjectDistinct(newData, clinicSchedule.field)
        setClinicSchedule({ ...clinicSchedule, loading: false, options: newData })
      }
    }

    fetchData()
    fetchDataID()
    fetchDataFacilityName()
    fetchDataDoctorName()
    fetchDataSpecialization()
    fetchDataClinicSchedule()

    return () => {
      attached = false
    }
  }, [filter])

  React.useEffect(() => {
    const filterObject = {
      ...(id.value ? { id: id.value.id } : {}),
      ...(specialization.value ? { 'specialization.main': specialization.value.specialization } : {}),
      ...(doctorName.value ? { full_name: doctorName.value.full_name } : {}),
      ...(facilityName.value ? { 'facility_info.name': facilityName.value.facilityName } : {}),
      ...(clinicSchedule.value ? { 'facility_info.clinic_schedule': clinicSchedule.value.clinicSchedule } : {})
    }
    setFilter(filterObject)
  }, [id.value, status.value, facilityName.value, specialization.value, doctorName.value, clinicSchedule.value])

  React.useEffect(() => {
    let attached = true
    const fetchData = async () => {
      setTableData({ ...tableData, loading: true })
      const { data } = await ProfileService.getAllDoctors({
        type,
        limit: tableData.limit,
        page: tableData.page,
        filters: filter
      })
      if (attached) {
        let listOfDoctors = data.data ? data.data.list : []
        const count = data.data ? data.data.count : 0
        if (listOfDoctors) {
          listOfDoctors = listOfDoctors.map(obj => {
            return {
              status: obj.status,
              id: obj.id,
              doctorName: obj.full_name,
              specialization: obj.specialization,
              name: obj.facility ? obj.facility.name : '',
              clinicSchedule: obj.facility ? obj.facility.clinic_schedule : ''
            }
          })
        }
        setTableData({ ...tableData, loading: false, data: listOfDoctors, pageCount: count })
      }
    }
    fetchData()
    return () => {
      attached = false
    }
  }, [tableData.limit, tableData.page])

  const onHandleDoubleClick = objID => {
    setOpen(false)
    history.push(`${url}/${objID}`)
  }

  const onHandleSort = (field, isSort) => {
    let newList = []
    if (isSort) {
      newList = tableData.data.sort((a, b) => {
        if (a[field] < b[field]) return -1
        if (a[field] > b[field]) return 1
        return 0
      })
    } else {
      newList = tableData.data.reverse((a, b) => {
        if (a[field] < b[field]) return -1
        if (a[field] > b[field]) return 1
        return 0
      })
    }
    setTableData({ ...tableData, data: newList })
  }
  return (
    <React.Fragment>
      {isLoaded && (
        <Modal style={{ outline: 'none' }} open={open} onClose={() => setOpen(false)}>
          <div className={classes.root} style={{ outline: 'none' }}>
            <div id='tableWrapper' className={classes.tableWrapper}>
              <TableContainer style={{ height: 350, backgroundColor: '#f5f7fb' }}>
                <Table stickyHeader className={classes.table} size='small'>
                  <TableHead className={classes.tableHeader}>
                    <TableRow>
                      <PartnerHeaderCell
                        headerTitle='Status'
                        width={{ width: '10%', verticalAlign: 'top' }}
                        enableSort={false}
                        enableAutoComplete={false}
                      />
                      <PartnerHeaderCell
                        headerTitle='Medical Partner ID'
                        width={{ width: '14%' }}
                        field={id.field}
                        loading={id.loading}
                        inputValue={id.inputValue}
                        value={id.value}
                        options={id.options}
                        onChangeValue={value => {
                          setID({ ...id, value })
                        }}
                        onClickSort={(field, sort) => onHandleSort(field, sort)}
                        onChangeInputValue={inputValue => setID({ ...id, inputValue })}
                      />
                      <PartnerHeaderCell
                        headerTitle='Doctor Name'
                        width={{ width: '18%' }}
                        field={doctorName.field}
                        loading={doctorName.loading}
                        inputValue={doctorName.inputValue}
                        value={doctorName.value}
                        options={doctorName.options}
                        onChangeValue={value => setDoctorName({ ...doctorName, value })}
                        onClickSort={(field, sort) => onHandleSort(field, sort)}
                        onChangeInputValue={inputValue => setDoctorName({ ...doctorName, inputValue })}
                      />
                      <PartnerHeaderCell
                        headerTitle='Specialization'
                        width={{ width: '16%' }}
                        field={specialization.field}
                        loading={specialization.loading}
                        inputValue={specialization.inputValue}
                        value={specialization.value}
                        options={specialization.options}
                        onChangeValue={value => setSpecialization({ ...specialization, value })}
                        onClickSort={(field, sort) => onHandleSort(field, sort)}
                        onChangeInputValue={inputValue => setSpecialization({ ...specialization, inputValue })}
                      />
                      <PartnerHeaderCell
                        headerTitle='Facility Name'
                        width={{ width: '18%' }}
                        field={facilityName.field}
                        loading={facilityName.loading}
                        inputValue={facilityName.inputValue}
                        value={facilityName.value}
                        options={facilityName.options}
                        onChangeValue={value => {
                          setFacilityName({ ...facilityName, value })
                        }}
                        onClickSort={(field, sort) => onHandleSort(field, sort)}
                        onChangeInputValue={inputValue => setFacilityName({ ...facilityName, inputValue })}
                      />
                      <PartnerHeaderCell
                        headerTitle='Clinic Schedule'
                        width={{ width: '16%' }}
                        field={clinicSchedule.field}
                        loading={clinicSchedule.loading}
                        inputValue={clinicSchedule.inputValue}
                        value={clinicSchedule.value}
                        options={clinicSchedule.options}
                        onChangeValue={value => setClinicSchedule({ ...clinicSchedule, value })}
                        onClickSort={(field, sort) => onHandleSort(field, sort)}
                        onChangeInputValue={inputValue => setClinicSchedule({ ...clinicSchedule, inputValue })}
                      />
                    </TableRow>
                  </TableHead>
                  <TableBody className={classes.tableBody}>
                    <React.Fragment>
                      {tableData.loading ? (
                        <TableRow>
                          <TableCell colSpan={6}>
                            <div className={classes.loadingComponent}>
                              <CircularProgress />
                            </div>
                          </TableCell>
                        </TableRow>
                      ) : (
                        <React.Fragment>
                          {tableData.data.map((dataObj, dataIndex) => {
                            return (
                              <TableRow
                                onDoubleClick={() => {
                                  onHandleDoubleClick(dataObj.id)
                                }}
                                key={`dataList_${dataIndex}`}
                                className={classes.tableBodyRow}
                              >
                                {Object.keys(dataObj).map((objKey, keyIndex) => {
                                  return (
                                    <TableCell key={`objKey_${keyIndex}`} align='left'>
                                      {dataObj[objKey]}
                                    </TableCell>
                                  )
                                })}
                              </TableRow>
                            )
                          })}
                        </React.Fragment>
                      )}
                    </React.Fragment>
                  </TableBody>
                </Table>
              </TableContainer>
              <TablePagination
                page={tableData.page}
                total={tableData.data.length}
                pageCount={tableData.pageCount}
                limit={tableData.limit}
                text={paginationText}
                onClickLimit={val => setTableData({ ...tableData, limit: val, page: 1 })}
                onClickPage={val => setTableData({ ...tableData, page: val })}
              />
            </div>
            <div className={classes.footer}>
              <Button onClick={() => setOpen(false)} id='rc_btn_cancel' className={classes.btnRed} variant='contained'>
                Close
              </Button>
            </div>
          </div>
        </Modal>
      )}
    </React.Fragment>
  )
}

export default DoctorListView
