import React from 'react'
import InputBase from '@material-ui/core/InputBase'
import NativeSelect from '@material-ui/core/NativeSelect'
import MenuItem from '@material-ui/core/MenuItem'
import NumberFormat from 'react-number-format'
import Select from '@material-ui/core/Select'

import classes from './InputComponents.module.scss'

export const StatusSelectComponent = props => {
  const { id = '', value, onChange } = props
  return (
    <NativeSelect id={id} value={value} onChange={onChange}>
      <option value={'Active'}>Active</option>
      <option value={'On-Hold'}>On-Hold</option>
      <option value={'Inactive'}>Inactive</option>
    </NativeSelect>
  )
}

export const TextInputComponent = props => {
  const { id = '', error = '', value, onChange } = props
  return (
    <React.Fragment>
      <InputBase
        value={value}
        type='text'
        onChange={onChange}
        className={error && !value ? classes.inputFieldsError : classes.inputFields}
        autoComplete='off'
      />
    </React.Fragment>
  )
}

export const NumberInputComponent = props => {
  const { id = '', error = '', disabled = false, value, onChange } = props
  return (
    <React.Fragment>
      <NumberFormat
        id={id}
        className={error && !value ? classes.inputFieldsError : classes.inputFields}
        disabled={disabled}
        value={value}
        onValueChange={value => onChange(value.formattedValue)}
        decimalSeparator={false}
        customInput={InputBase}
        isNumericString
        allowLeadingZeros
        allowNegative={false}
        displayType={'input'}
      />
    </React.Fragment>
  )
}
