// import Axios from 'axios'
import instance from '../instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'

const axios = instance('/p/f')
const axiosPd = instance('/p/d')
const axiosPos = instance('/p/os')
const axiosCom = instance('p/c')

export default class ProfileService {
  static getFacilityFieldData (params) {
    return axios({
      url: `/partners/facilities/partner/type/list/field`,
      method: 'GET',
      params
    })
  }

  static getFacilityTableData (params) {
    return axios({
      url: `/partners/facilities/partner/type/list`,
      method: 'GET',
      params
    })
  }

  static getRoomAndBoard (id) {
    return axios({
      url: `/partners/facilities/profile/room-and-board/${id}`,
      method: 'GET'
    })
  }

  static updateHospitalProfile (id, data) {
    return axios({
      url: `/partners/facilities/partner/profile/${id}`,
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static updateAndCreateRoomBoard (data) {
    return axios({
      url: '/partners/facilities/profile/room-and-board',
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static updateDoctorProfile (id, data) {
    return axiosPd({
      url: `/partners/doctors/partner/profile/${id}`,
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static updateDentalClinicProfile (id, data) {
    return axios({
      url: `/partners/facilities/partner/profile/${id}`,
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static updateMedicalClinicProfile (id, data) {
    return axios({
      url: `/partners/facilities/partner/profile/${id}`,
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static updateSpecialOtherServices (data) {
    return axiosPos({
      url: `/partners/other-services/profile/update`,
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static updateStandardRates (id, data) {
    return axios({
      url: `/partners/facilities/profile/standard-rates/${id}`,
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static updateListOfAffiliatedDoctors (data) {
    return axios({
      url: '/partners/facilities/profile/affiliated-doctor',
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static updatedListOfAffiliatedFacilities (data) {
    return axiosPd({
      url: '/partners/doctors/profile/affiliated-facilities',
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static updateFacilitiesContacts (data) {
    return axios({
      url: '/partners/facilities/medical/profile/contacts',
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static updateDoctorContacts (data) {
    return axiosPd({
      url: '/partners/doctors/medical/profile/contacts',
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static getStandardRates (id) {
    return axios({
      url: `/partners/facilities/profile/standard-rates/${id}`,
      method: 'GET',
      headers: { skip_generate_form_data: 1 }
    })
  }

  static getDoctorsById (id) {
    return axiosPd({
      url: `/partners/doctors/${id}`,
      method: 'GET'
    })
  }

  static getClinicById (id) {
    return axios({
      url: `/partners/facilities/id/${id}`,
      headers: { skip_generate_form_data: 1 },
      method: 'GET'
    })
  }

  static getClinicByType (type) {
    return axios({
      url: `/partners/facilities/medical/profiles?stage=PROFILE_GENERATED&type=${type}`,
      method: 'GET'
    })
  }

  static getDoctorByType (type) {
    return axiosPd({
      url: `/partners/doctors/medical/profiles?stage=PROFILE_GENERATED&type=${type}`,
      method: 'GET'
    })
  }

  static getAllDoctors (params) {
    return axiosPd({
      url: `/partners/doctors/medical/list`,
      method: 'GET',
      params
    })
  }

  static getDoctorsField (params) {
    return axiosPd({
      url: '/partners/doctors/medical/field',
      method: 'GET',
      params
    })
  }

  static getAffiliatedDoctors (facilityId, type) {
    return axios({
      url: `/partners/facilities/profile/affiliated-doctor/${facilityId}?type=${type}`,
      method: 'GET'
    })
  }

  static getAffiliatedFacility (doctorId, type) {
    return axiosPd({
      url: `/partners/doctors/profile/affiliated-facilities/${doctorId}?type=${type}`,
      method: 'GET'
    })
  }

  static getSpecialOtherServices () {
    return axiosPos({
      url: `/partners/other-services/profile/list?stage=PROFILE_GENERATED`,
      method: 'GET'
    })
  }

  static getFacilityDiscount (id) {
    return axios({
      url: `/partners/facility_discounts/${id}`,
      method: 'GET'
    })
  }

  static updateFacilityDiscount (id, data) {
    return axios({
      url: `/partners/facility_discount/${id}`,
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static deleteOneFacilityDiscount (id) {
    return axios({
      url: `/partners/facility_discount/${id}`,
      method: 'DELETE',
      headers: { skip_generate_form_data: 1 }
    })
  }

  static getUploadFile (id) {
    return axiosCom({
      url: `/partners/common/files/${id}`,
      method: 'GET',
      headers: { skip_generate_form_data: 1 }
    })
  }

  static uploadFile (data) {
    return axiosCom({
      url: '/partners/common/file-upload',
      method: 'POST',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static deleteUploadFile (param) {
    return axiosCom({
      url: `/partners/common/file-upload/delete/${param}`,
      method: 'DELETE',
      headers: { skip_generate_form_data: 1 }
    })
  }
}
