import React from 'react'
import Modal from '@material-ui/core/Modal'
import Table from '@material-ui/core/Table'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableContainer from '@material-ui/core/TableContainer'
import TableRow from '@material-ui/core/TableRow'
import TableBody from '@material-ui/core/TableBody'
import Button from '@material-ui/core/Button'
import CircularProgress from '@material-ui/core/CircularProgress'
import ProfileService from 'Services/Api/Partner/ProfileService'
import PartnerHeaderCell from '../../../Components/PartnerHeaderCell/PartnerHeaderCell'
import TablePagination from '../../../Components/TablePagination/TablePagination'
import { arrayObjectNullFilter, arrayObjectDistinct } from 'Utilities/Partner/ArrayUtilities'
import useDebounce from '../../../Components/CustomHooks/useDebounce'
import { useHistory } from 'react-router-dom'

import classes from './FacilityListView.module.scss'

const FacilityListView = props => {
  const { open, setOpen, paginationText = '', url = '', type = '' } = props
  const isAttached = React.useRef(true)

  const history = useHistory()
  const [isLoaded, setIsLoaded] = React.useState(false)
  const [filter, setFilter] = React.useState({})

  const [status, setStatus] = React.useState({
    field: 'status',
    loading: false,
    value: '',
    inputValue: '',
    options: []
  })
  const [id, setID] = React.useState({
    field: 'id',
    loading: false,
    value: '',
    inputValue: '',
    options: []
  })
  const [facilityName, setFacilityName] = React.useState({
    field: 'name',
    loading: false,
    value: '',
    inputValue: '',
    options: []
  })
  const [location, setLocation] = React.useState({
    field: 'location',
    loading: false,
    value: null,
    inputValue: '',
    options: []
  })
  const [tableData, setTableData] = React.useState({
    loading: false,
    data: [],
    page: 1,
    pageCount: 1,
    limit: 10
  })

  const IDInput = useDebounce(id.inputValue, 500)
  const statusInput = useDebounce(status.inputValue, 500)
  const facilityNameInput = useDebounce(facilityName.inputValue, 500)
  const locationInput = useDebounce(location.inputValue, 500)

  React.useEffect(() => {
    setIsLoaded(true)
    return () => {
      isAttached.current = false
    }
  }, [])

  React.useEffect(() => {
    let attached = true
    const fetchDataID = async () => {
      setID({ ...id, loading: true })
      const { data } = await ProfileService.getFacilityFieldData({
        type: type,
        field: id.field,
        value: id.inputValue,
        filters: filter
      })
      if (attached) {
        let newData = arrayObjectNullFilter(data.data, id.field)
        newData = arrayObjectDistinct(newData, id.field)
        setID({ ...id, loading: false, options: newData })
      }
    }
    if (id.inputValue.length >= 6) {
      fetchDataID()
    }
    return () => {
      attached = false
    }
  }, [IDInput])

  React.useEffect(() => {
    let attached = true
    const fetchDataStatus = async () => {
      setStatus({ ...status, loading: true })
      const { data } = await ProfileService.getFacilityFieldData({
        type: type,
        field: status.field,
        value: status.inputValue,
        filters: filter
      })
      if (attached) {
        let newData = arrayObjectNullFilter(data.data, status.field)
        newData = arrayObjectDistinct(newData, status.field)
        setStatus({ ...status, loading: false, options: newData })
      }
    }
    if (status.inputValue.length >= 3) {
      fetchDataStatus()
    }
    return () => {
      attached = false
    }
  }, [statusInput])

  React.useEffect(() => {
    let attached = true
    const fetchDataFacilityName = async () => {
      setFacilityName({ ...facilityName, loading: true })
      const { data } = await ProfileService.getFacilityFieldData({
        type: type,
        field: facilityName.field,
        value: facilityName.inputValue,
        filters: filter
      })
      if (attached) {
        let newData = arrayObjectNullFilter(data.data, facilityName.field)
        newData = arrayObjectDistinct(newData, facilityName.field)
        setFacilityName({ ...facilityName, loading: false, options: newData })
      }
    }
    if (facilityName.inputValue.length >= 3) {
      fetchDataFacilityName()
    }
    return () => {
      attached = false
    }
  }, [facilityNameInput])

  React.useEffect(() => {
    let attached = true
    const fetchDataLocation = async () => {
      setLocation({ ...location, loading: true })
      const { data } = await ProfileService.getFacilityFieldData({
        type: type,
        field: location.field,
        value: location.inputValue,
        filters: filter
      })
      if (attached) {
        let newData = arrayObjectNullFilter(data.data, location.field)
        newData = arrayObjectDistinct(newData, location.field)
        setLocation({ ...location, loading: false, options: newData })
      }
    }
    if (location.inputValue.length >= 3) {
      fetchDataLocation()
    }
    return () => {
      attached = false
    }
  }, [locationInput])

  React.useEffect(() => {
    let attached = true
    const fetchData = async () => {
      setTableData({ ...tableData, loading: true })
      const { data } = await ProfileService.getFacilityTableData({
        type,
        limit: tableData.limit,
        page: tableData.page,
        filters: filter
      })
      if (attached) {
        setTableData({ ...tableData, loading: false, data: data.data.list, pageCount: data.data.count, page: 1 })
      }
    }

    const fetchDataID = async () => {
      setID({ ...id, loading: true })
      const { data } = await ProfileService.getFacilityFieldData({
        type: type,
        field: id.field,
        value: id.value,
        filters: filter
      })
      if (attached) {
        let newData = arrayObjectNullFilter(data.data, id.field)
        newData = arrayObjectDistinct(newData, id.field)
        setID({ ...id, loading: false, options: newData })
      }
    }

    const fetchDataStatus = async () => {
      setStatus({ ...status, loading: true })
      const { data } = await ProfileService.getFacilityFieldData({
        type: type,
        field: status.field,
        value: status.value,
        filters: filter
      })
      if (attached) {
        let newData = arrayObjectNullFilter(data.data, status.field)
        newData = arrayObjectDistinct(newData, status.field)
        setStatus({ ...status, loading: false, options: newData })
      }
    }

    const fetchDataFacilityName = async () => {
      setFacilityName({ ...facilityName, loading: true })
      const { data } = await ProfileService.getFacilityFieldData({
        type: type,
        field: facilityName.field,
        value: facilityName.value,
        filters: filter
      })
      if (attached) {
        let newData = arrayObjectNullFilter(data.data, facilityName.field)
        newData = arrayObjectDistinct(newData, facilityName.field)
        setFacilityName({ ...facilityName, loading: false, options: newData })
      }
    }

    const fetchDataLocation = async () => {
      setLocation({ ...location, loading: true })
      const { data } = await ProfileService.getFacilityFieldData({
        type: type,
        field: location.field,
        value: location.value,
        filters: filter
      })
      if (attached) {
        let newData = arrayObjectNullFilter(data.data, location.field)
        newData = arrayObjectDistinct(newData, location.field)
        setLocation({ ...location, loading: false, options: newData })
      }
    }
    fetchData()
    fetchDataID()
    fetchDataStatus()
    fetchDataFacilityName()
    fetchDataLocation()

    return () => {
      attached = false
    }
  }, [filter])

  React.useEffect(() => {
    const filterObject = {
      ...(id.value ? { id: id.value.id } : {}),
      ...(status.value ? { status: status.value.status } : {}),
      ...(facilityName.value ? { name: facilityName.value.name } : {}),
      ...(location.value ? { location: location.value.location } : {})
    }
    setFilter(filterObject)
  }, [id.value, status.value, facilityName.value, location.value])

  React.useEffect(() => {
    let attached = true
    const fetchData = async () => {
      setTableData({ ...tableData, loading: true })
      const { data } = await ProfileService.getFacilityTableData({
        type,
        limit: tableData.limit,
        page: tableData.page,
        filters: filter
      })
      if (attached) {
        setTableData({ ...tableData, loading: false, data: data.data.list, pageCount: data.data.count })
      }
    }
    fetchData()
    return () => {
      attached = false
    }
  }, [tableData.limit, tableData.page])

  const onHandleDoubleClick = objID => {
    setOpen(false)
    history.push(`${url}/${objID}`)
  }

  const onHandleSort = (field, isSort) => {
    let newList = []
    if (isSort) {
      newList = tableData.data.sort((a, b) => {
        if (a[field] < b[field]) return -1
        if (a[field] > b[field]) return 1
        return 0
      })
    } else {
      newList = tableData.data.reverse((a, b) => {
        if (a[field] < b[field]) return -1
        if (a[field] > b[field]) return 1
        return 0
      })
    }
    setTableData({ ...tableData, data: newList })
  }
  return (
    <React.Fragment>
      {isLoaded && (
        <Modal style={{ outline: 'none' }} open={open} onClose={() => setOpen(false)}>
          <div className={classes.root} style={{ outline: 'none' }}>
            <div id='tableWrapper' className={classes.tableWrapper}>
              <TableContainer style={{ height: 350, backgroundColor: '#f5f7fb' }}>
                <Table stickyHeader className={classes.table} size='small'>
                  <TableHead className={classes.tableHeader}>
                    <TableRow>
                      <PartnerHeaderCell
                        headerTitle='Status'
                        width={{ width: '10%', verticalAlign: 'top' }}
                        enableSort={false}
                        enableAutoComplete={false}
                      />
                      <PartnerHeaderCell
                        headerTitle='Medical Partner ID'
                        width={{ width: '20%' }}
                        field={id.field}
                        loading={id.loading}
                        inputValue={id.inputValue}
                        value={id.value}
                        options={id.options}
                        onChangeValue={value => {
                          setID({ ...id, value })
                        }}
                        onClickSort={(field, sort) => onHandleSort(field, sort)}
                        onChangeInputValue={inputValue => setID({ ...id, inputValue })}
                      />
                      <PartnerHeaderCell
                        headerTitle='Facility Name'
                        width={{ width: '35%' }}
                        field={facilityName.field}
                        loading={facilityName.loading}
                        inputValue={facilityName.inputValue}
                        value={facilityName.value}
                        options={facilityName.options}
                        onChangeValue={value => setFacilityName({ ...facilityName, value })}
                        onClickSort={(field, sort) => onHandleSort(field, sort)}
                        onChangeInputValue={inputValue => setFacilityName({ ...facilityName, inputValue })}
                      />
                      <PartnerHeaderCell
                        headerTitle='Location'
                        width={{ width: '30%' }}
                        field={location.field}
                        loading={location.loading}
                        inputValue={location.inputValue}
                        value={location.value}
                        options={location.options}
                        onChangeValue={value => setLocation({ ...location, value })}
                        onClickSort={(field, sort) => onHandleSort(field, sort)}
                        onChangeInputValue={inputValue => setLocation({ ...location, inputValue })}
                      />
                    </TableRow>
                  </TableHead>
                  <TableBody className={classes.tableBody}>
                    <React.Fragment>
                      {tableData.loading ? (
                        <TableRow>
                          <TableCell colSpan={4}>
                            <div className={classes.loadingComponent}>
                              <CircularProgress />
                            </div>
                          </TableCell>
                        </TableRow>
                      ) : (
                        <React.Fragment>
                          {tableData.data.map((dataObj, dataIndex) => {
                            return (
                              <TableRow
                                onDoubleClick={() => {
                                  onHandleDoubleClick(dataObj.id)
                                }}
                                key={`dataList_${dataIndex}`}
                                className={classes.tableBodyRow}
                              >
                                {Object.keys(dataObj).map((objKey, keyIndex) => {
                                  return (
                                    <TableCell key={`objKey_${keyIndex}`} align='left'>
                                      {dataObj[objKey]}
                                    </TableCell>
                                  )
                                })}
                              </TableRow>
                            )
                          })}
                        </React.Fragment>
                      )}
                    </React.Fragment>
                  </TableBody>
                </Table>
              </TableContainer>
              <TablePagination
                page={tableData.page}
                total={tableData.data.length}
                pageCount={tableData.pageCount}
                limit={tableData.limit}
                text={paginationText}
                onClickLimit={val => setTableData({ ...tableData, limit: val, page: 1 })}
                onClickPage={val => setTableData({ ...tableData, page: val })}
              />
            </div>
            <div className={classes.footer}>
              <Button onClick={() => setOpen(false)} id='rc_btn_cancel' className={classes.btnRed} variant='contained'>
                Close
              </Button>
            </div>
          </div>
        </Modal>
      )}
    </React.Fragment>
  )
}

export default FacilityListView
